.nav{height:4rem;width:100%;background-color:#fdb927;display:flex;justify-content:space-between;align-items:center;position:-webkit-sticky;position:sticky;top:0;z-index:2;border-bottom:2px solid #552583}.nav .logo{background-image:url(/static/media/los-angeles-lakers-logo-font.8a58eaed.png);margin-left:1rem;background-size:cover;width:6rem;height:3rem}

.Ul{list-style:none;display:flex}@media (max-width: 768px){.Ul{flex-direction:column;position:fixed;top:0;right:0;height:100vh;width:10rem;padding-top:4rem;background-color:#552583;transition:transform 0.5s ease-in-out;z-index:3}}.Ul .link{text-decoration:none}.Ul li{color:white;padding:1rem;font-family:"Alfa Slab One", cursive;font-weight:100;font-size:1rem;transition:0.4s ease-in-out all}.Ul li:hover{background-color:#fdb927;cursor:pointer;text-shadow:0 0 10px #ffff;color:#552583}.control-arrow control-next{z-index:1;background-color:red;color:red}

.button{width:1.2rem;height:1.2rem;background-size:cover;margin-right:1rem;z-index:4;background-image:url(/static/media/basketball-ball.cc5a323d.png)}@-webkit-keyframes bounce{0%,10%,50%,70%,80%,90%,100%{transform:translateY(0)}40%{transform:translateY(-15px)}60%{transform:translateY(-5px)}}@keyframes bounce{0%,10%,50%,70%,80%,90%,100%{transform:translateY(0)}40%{transform:translateY(-15px)}60%{transform:translateY(-5px)}}.button:hover{cursor:pointer}@media (min-width: 768px){.button{display:none}}

.footer{width:100%;background:#2a2a2a;display:flex;flex-direction:column;justify-content:center;align-items:center}.footer .row{height:8rem;width:100%;display:flex;justify-content:space-evenly;align-items:center}.footer .row .icon{height:2rem;width:2rem;color:#9b9999;transition:0.4s ease-in-out all}.footer .row .icon:hover{color:#fdb927;cursor:pointer}.footer .footer_bottom{height:2.5rem;width:100%;background-color:#552583;color:#fdb927;display:flex;justify-content:center;align-items:center}.footer .footer_bottom h3{text-align:center;font-size:0.8rem}

.hero{height:36rem;width:100%;background-size:cover;background-repeat:no-repeat;background-position-x:center;z-index:-1}.hero__description{height:100%;width:100%;display:flex;justify-content:center;align-items:flex-end}.hero__description h3{color:white;font-size:2rem;text-align:center;text-justify:center;margin-bottom:2rem;font-family:"Alfa Slab One",cursive;font-weight:200;text-shadow:0 0 10px #000}

.news{width:100%;background-color:#fdb927;display:flex;flex-direction:column;align-items:center;justify-content:center}.news h3{text-align:center;color:white;font-family:"Alfa Slab One",cursive;text-shadow:0 0 10px #000;font-weight:100;font-size:2rem}.news .carousel-root{padding:2rem 1rem 0.5rem 1rem;width:80%;max-width:70rem}.news .carousel-slider{z-index:1}

.newsCard{min-height:30rem;height:20%;color:white;width:100%;background-position:top;background-repeat:no-repeat;display:flex;justify-content:center;align-items:flex-end;background-color:transparent}.newsCard .description{width:80%;margin-bottom:4rem;text-shadow:0 0 10px #000;font-size:1.3rem;font-family:"Roboto",sans-serif}

.nextMatch{padding:3rem 0 3rem 0;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#fdb927}.nextMatch h2{margin-bottom:2rem;color:white;font-family:"Alfa Slab One",cursive;font-weight:100;font-size:2rem;text-shadow:0 0 10px #000}.nextMatch__card{width:90%;height:10rem;display:flex;flex-direction:column;justify-content:space-between;align-items:center;background-color:#552583;border:8px solid white;border-radius:30px}@media (min-width: 1024px) and (max-width: 1920px){.nextMatch__card{max-width:40rem}}.nextMatch__card h3{color:white;font-family:"Alfa Slab One",cursive;font-weight:100;font-size:1.5rem}.nextMatch__card .middle{width:100%;height:100%;display:flex;align-items:center;justify-content:space-between}.nextMatch__card .matchDay{font-size:1.2rem;margin-top:0.5rem;margin-bottom:-1rem}

.shopContainer{width:100%;background-image:url(/static/media/hero2.39adf695.jpg);background-size:cover}.shopContainer h3{padding-top:2rem;text-align:center;color:white;font-family:"Alfa Slab One",cursive;text-shadow:0 0 10px #000;font-weight:100;font-size:1.2rem}.shopContainer .carousel-root{padding:2rem 2rem 0.5rem 2rem}.shopContainer .carousel .slide{background:none}

body{background-color:black;background-position:center;background-size:contain;background-repeat:repeat;height:6rem;width:100%}.squad__hero{height:7rem;width:100%;background-color:#fdb927;display:flex;flex-direction:column;justify-content:center;align-items:center}.squad__hero h3{color:#552583;font-size:2.5rem;font-family:"Alfa Slab One",cursive;font-weight:200;text-shadow:-5px 4px 2px white}.squad__blur{box-shadow:0 0 8px 8px #fdb927}

.squad .cardPlayer{width:300px;justify-self:center}.squad__grid{padding:3rem 2rem 3rem 2rem;display:grid;grid-gap:2rem;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));font-family:"Roboto",sans-serif}.squad__grid__title{width:100%;height:3rem;display:flex;margin-bottom:3rem;align-items:center;justify-content:flex-start;color:#fdb927}.squad__grid__title__number{width:3rem;height:100%;align-self:center;justify-self:center;text-align:center;font-size:1.5rem;color:white}.squad__grid__title__name{display:flex;flex-direction:column;margin-left:1rem}.squad__grid__title__name p{color:white;font-size:1.5rem}.squad__grid__title__name h3{font-size:1.5rem}.squad__grid__img{height:22rem;width:100%;background-position:center;justify-self:center;align-self:center;cursor:pointer}.squad__grid__img:hover .squad__grid__img__overlay{opacity:1}.squad__grid__img__overlay{width:100%;height:100%;align-items:flex-end;opacity:0;background:rgba(95,95,95,0.726);color:#fef5df;transition:all 0.5s ease-in-out}.squad__grid__img__overlay__desc{width:100%;padding-top:9rem;display:flex;flex-direction:column}.squad__grid__img__overlay__desc__row{display:flex;width:100%;height:2rem;justify-content:space-between;align-items:center;margin:0;padding:0}.squad__grid__img__overlay__desc__row h4{margin-left:1.5rem;margin-right:1.5rem;height:10px;font-size:0.8rem}.squad__grid__img__overlay__desc hr{width:99%}

.modal{height:100vh;width:100vw;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:white}.modal h3{margin-top:20px;font-size:1.8rem;font-family:"Alfa Slab One",cursive;font-weight:100;color:#552583;text-shadow:-5px 4px 2px #fdb927;text-align:center}

.shop{background-color:white;width:100%}.shop__hero{display:flex;justify-content:center;align-items:center;height:15rem}.shop__hero__background{width:100%;height:100%;-webkit-filter:grayscale(80%);filter:grayscale(80%)}.shop__hero__title{position:absolute}.shop__hero__title h2{color:#fdb927;text-shadow:0px 0px 20px #552583;font-family:"Alfa Slab One",cursive;font-size:2.5rem}.shop__container__title{text-align:center;padding:1rem;font-size:2rem;font-family:"Alfa Slab One",cursive;font-weight:100;background:#431d66;border-top:5px solid white;border-bottom:5px solid white}.shop__container__title h3{font-weight:100;color:#fdfcfc;text-shadow:-4px 3px 0px #815a06;letter-spacing:4px}.shop__container__title p{margin-top:1rem;font-size:1.2rem;color:#fdb927;text-shadow:-4px 3px 0px #32164d;cursor:pointer;transition:0.4s ease-in-out all}.shop__container__title p:hover{color:#fdfcfc;text-shadow:-4px 5px 0px #32164d}.shop__container__title p span{text-shadow:none}.shop__container__list{display:grid;grid-gap:1rem;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr));width:100%;height:100%;margin-top:2rem}.react-reveal{justify-self:center}

.shopItem{width:12rem;height:23rem;background-color:#fdfdfd;border:5px solid #552583;margin-bottom:2rem;display:flex;flex-direction:column;justify-content:center;align-items:center;cursor:pointer;align-self:center;justify-self:center}.shopItem__img img{width:12rem;height:15rem}.shopItem__description{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;font-family:"Roboto",sans-serif}.shopItem__description h3{font-size:1.2rem;width:10rem}.shopItem__description h4{color:#552583;margin-top:0.5rem;margin-bottom:0.5rem}.shopItem__overlay{opacity:0;position:absolute;background:transparent;transition:1s ease-in-out all;display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:rgba(221,189,4,0.521);width:12rem;height:23rem}.shopItem__overlay:hover{opacity:1;display:visible}.shopItem__overlay button{width:10rem;height:3rem;margin-bottom:1rem;background-color:#fdb927;font-size:1rem;outline:none;font-family:"Alfa Slab One",cursive;padding:0.5rem;border:5px solid #552583;border-radius:20px;cursor:pointer;color:#552583;transition:0.4s ease-in-out all}.shopItem__overlay button:hover{color:white}

.vacio{margin:1rem;text-align:center;font-family:"Roboto",sans-serif;font-size:1.4rem}.vacio__link{text-decoration:none;color:purple;font-weight:700;transition:0.3s ease-in-out all}.vacio__link:hover{color:#fdb927}.Checkout{background-color:white}.Checkout__container{min-height:40vh;width:100%}.Checkout__container h2{text-align:center;width:100%;font-family:"Alfa Slab One",cursive;color:#552583;border-bottom:3px solid #fdb927;font-weight:100;font-size:2rem;padding-top:1rem;padding-bottom:0.5rem}.Checkout__container__list{display:grid;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr));grid-gap:1rem;margin-top:2rem}.Checkout__container__list__item{display:flex;align-items:center;justify-content:center;width:17rem;height:14rem;justify-self:center}.Checkout__container__list__item img{object-fit:contain;width:10rem;height:10rem}.Checkout__container__list__item__detail{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;font-family:"Roboto",sans-serif}.Checkout__container__list__item__detail h4{margin-top:0.5rem}.Checkout__container__list__item__detail button{border:none;width:7rem;height:2rem;border-radius:50px;background-color:#fdb927;margin-top:1rem;font-family:"Roboto",sans-serif;transition:0.8s ease-in-out all;cursor:pointer;outline:none;font-size:0.9rem}.Checkout__container__list__item__detail button:hover{transform:translateX(1rem)}.Checkout__container__list__item__detail button:nth-last-child(1){background-color:#e22e2e;color:white}

.subtotal{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;font-family:"Roboto",sans-serif;background-color:#552583;bottom:0}.subtotal__shipping{font-size:1.2rem;margin:1rem;height:100%;color:white}.subtotal__shipping__option{margin-bottom:1rem}.subtotal__shipping__option input{margin-right:1rem}.subtotal__total{font-size:1.3rem;text-align:center;height:100%;margin-bottom:1rem;color:#fdb927}.subtotal__payment{margin-bottom:1rem}.subtotal__payment button{height:2.5rem;background-color:#fdb927;font-weight:700;border:none;outline:none;font-family:"Roboto",sans-serif;padding:0.5rem;border-radius:50px;transition:0.3s ease-in-out all;cursor:pointer}.subtotal__payment button:hover{box-shadow:0 0 5px white}

.iconCheckout{color:white;text-align:center;position:fixed;height:2rem;width:2rem;background-color:orange;border-radius:999px;padding:0.1rem;bottom:0;right:0;margin-bottom:3.5rem;margin-right:1rem;border:2px solid black;z-index:2}.iconCheckout__link{text-decoration:none;color:#552583}

.aboutus{background-color:white;width:100%;display:flex;align-items:flex-start;justify-content:center;min-height:90vh}.aboutus__container{width:100%;height:100%}.aboutus__container p{font-family:"Roboto",sans-serif;font-size:1.3rem;text-align:center;padding:1rem}.aboutus__container__title{background-color:#552583;width:100%;height:5rem;display:flex;justify-content:center;align-items:center}.aboutus__container__title h2{color:white;font-size:1.5rem;letter-spacing:0.3rem;font-family:"Alfa Slab One",cursive;font-weight:100}.aboutus__container__socialHero{height:10rem;width:100%;display:flex;align-items:center;justify-content:center}.aboutus__container__socialHero__banner{width:100%;height:100%;position:relative;background-position:center;background-size:cover;-webkit-filter:blur(0.9px) grayscale(60%);filter:blur(0.9px) grayscale(60%)}.aboutus__container__socialHero p{font-size:1.5rem;font-family:"Alfa Slab One",cursive;font-weight:700;color:#552583;position:absolute;text-shadow:0px 5px 4px #fdb927}.aboutus__container__socialdev{margin-top:1rem;margin-bottom:1rem;width:100%;display:flex;justify-content:space-around;align-items:center}.aboutus__container__socialdev__icon{width:1.5rem;height:1.5rem;color:#552583;transition:0.5s ease-in-out all;cursor:pointer}.aboutus__container__socialdev__icon:hover{transform:translateY(-10px);color:#fdb927}.aboutus__container img{width:100%;height:5rem;justify-self:center;object-fit:cover}

.productDetail{width:100%;background-color:white;min-height:90vh}.productDetail__container{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:2rem;font-family:"Roboto",sans-serif}.productDetail__container img{object-fit:contain;height:20rem}.productDetail__container h2{text-align:center}.productDetail__container h3{margin:1rem;color:#552583}.productDetail__container h4{margin-top:1.5rem;font-size:1.8rem}.productDetail__container p{margin-top:1rem;text-align:center}.productDetail__container button{margin-top:1rem;padding:1rem;background-color:#fdb927;border:2px solid #552583;border-radius:999px;font-size:1rem;font-weight:600;cursor:pointer;outline:none;transition:0.3s ease-in-out all}.productDetail__container button:hover{box-shadow:0px 0px 10px #552583}

